import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Header.css';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleNavbar = () => setIsOpen(!isOpen);

  useEffect(() => {
    // Set gtranslate settings
    (window as any).gtranslateSettings = {
      default_language: 'en',
      languages: ['en','fr','it','es'],
      wrapper_selector: '.gtranslate_wrapper',
      flag_size: 16,
      switcher_text_color: '#f7f7f7',
      switcher_arrow_color: '#f2f2f2',
      switcher_border_color: '#161616',
      switcher_background_color: '#303030',
      switcher_background_shadow_color: '#474747',
      switcher_background_hover_color: '#3a3a3a',
      dropdown_text_color: '#eaeaea',
      dropdown_hover_color: '#748393',
      dropdown_background_color: '#474747'
    };

    // Load gtranslate script
    const script = document.createElement('script');
    script.src = 'https://cdn.gtranslate.net/widgets/latest/dwf.js';
    script.defer = true;
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  return (
    <header className="header">
      <nav className="navbar navbar-expand-lg navbar-dark custom-navbar">
        <a href="/"><img src="/logo_sc.png" alt="Logo" className="header-logo img-responsive" /></a>

        <button
          className="navbar-toggler"
          type="button"
          onClick={toggleNavbar}
          aria-controls="navbarCollapse"
          aria-expanded={isOpen}
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className={`collapse navbar-collapse ${isOpen ? 'show' : ''}`} id="navbarCollapse">
          <ul className="navbar-nav ms-auto">
            <li className="nav-item">
              <a href="/" className="nav-link">Home</a>
            </li>
            <li className="nav-item">
              <a href="#services" className="nav-link">Services</a>
            </li>
            <li className="nav-item">
              <a href="#projects" className="nav-link">Projects</a>
            </li>
            <li className="nav-item">
              <a href="#contact" className="nav-link">Contact</a>
            </li>
          </ul>
        </div>
      </nav>
      <div className="gtranslate_wrapper"></div>
    </header>
  );
}
